/* MagicBento Styles */
.bento-section {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  background: 
    radial-gradient(circle at 20% 30%, rgba(138, 43, 226, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(0, 191, 255, 0.06) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(64, 64, 64, 0.03) 0%, transparent 70%),
    linear-gradient(135deg, #000000 0%, #0a0a0a 50%, #000000 100%);
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  width: 100%;
  padding: 2rem;
}

.card {
  position: relative;
  padding: 2rem;
  border-radius: 20px;
  min-height: 280px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid rgba(128, 128, 128, 0.15);
  background: linear-gradient(135deg,
    rgba(32, 32, 32, 0.4) 0%,
    rgba(24, 24, 24, 0.5) 100%);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.5);
}

.card::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: inherit;
  padding: 1px;
  background: linear-gradient(
    135deg,
    rgba(192, 192, 192, 0.4) 0%,
    rgba(255, 255, 255, 0.1) 50%,
    rgba(192, 192, 192, 0.4) 100%
  );
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
          mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
          mask-composite: exclude;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.card:hover::before {
  opacity: 1;
}

/* Border Glow Effect */
.card--border-glow {
  --glow-x: 50%;
  --glow-y: 50%;
  --glow-intensity: 0;
  --glow-color: 192, 192, 192;
  --glow-radius: 300px;
}

.card--border-glow::after {
  content: '';
  position: absolute;
  inset: -1px;
  border-radius: inherit;
  background: radial-gradient(
    circle var(--glow-radius) at var(--glow-x) var(--glow-y),
    rgba(var(--glow-color), calc(var(--glow-intensity) * 0.6)),
    transparent
  );
  pointer-events: none;
  opacity: var(--glow-intensity);
  transition: opacity 0.3s ease;
  z-index: -1;
}

/* Card Content */
.card__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.card__label {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: 300;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
}

.card__content {
  z-index: 1;
}

.card__icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  filter: drop-shadow(0 0 15px rgba(138, 43, 226, 0.4)) drop-shadow(0 0 30px rgba(0, 191, 255, 0.2));
  transition: all 0.3s ease;
  display: inline-block;
}

.card:hover .card__icon {
  transform: scale(1.1);
  filter: drop-shadow(0 0 25px rgba(138, 43, 226, 0.6)) drop-shadow(0 0 50px rgba(0, 191, 255, 0.4));
}

.card__title {
  font-size: 1.5rem;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 0.5rem;
  line-height: 1.2;
  letter-spacing: 2px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
}

.card__description {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.45);
  line-height: 1.6;
  margin: 0;
  font-weight: 300;
  letter-spacing: 0.5px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
}

/* Text Auto-hide */
.card--text-autohide .card__description {
  opacity: 0.5;
  transition: opacity 0.3s ease;
}

.card--text-autohide:hover .card__description {
  opacity: 1;
}

/* Particle Container */
.particle-container {
  transform-style: preserve-3d;
}

.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  pointer-events: none;
  z-index: 100;
}

/* Global Spotlight */
.global-spotlight {
  position: fixed;
  pointer-events: none;
  z-index: 200;
  mix-blend-mode: screen;
}

/* Responsive */
@media (max-width: 768px) {
  .card-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 1rem;
  }

  .card {
    min-height: 200px;
    padding: 1.5rem;
  }

  .card__icon {
    font-size: 2.5rem;
  }

  .card__title {
    font-size: 1.5rem;
  }

  .card__description {
    font-size: 0.875rem;
  }
}

/* Animation Enhancements */
.card:hover {
  transform: translateY(-4px);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.7),
    0 0 40px rgba(138, 43, 226, 0.2);
  border-color: transparent;
  background: linear-gradient(135deg,
    rgba(48, 48, 48, 0.6) 0%,
    rgba(32, 32, 32, 0.7) 100%);
}

.card:active {
  transform: translateY(-2px);
}
